



*{
  box-sizing: border-box;  
  padding: 0;
  margin: 0;
  font-family: "Josefin", "Arial Narrow";
}

.header{
    width: 100%;
    line-height: 100px;
    background-color: #f5ba13;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 50px;
    color: white;
    box-shadow: 5px 3px 15px -5px black;
}

.main_note{
    width: 500px;
    padding:15px 10px 5px 10px;
    margin:20px auto;
}
 
 
.my_notes h1{
    color: #34495e;
}

.my_notes p{
    color: grey;
}

form{
    min-height: 50px;
    color: #ecf0f1;
    padding: 15px 10px 5px 10px;
    box-shadow: 3px 5px 15px -5px black;
    border-radius: 10px;
    position: relative;
}

input{
    width: 100%;
    line-height: 30px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 1.5rem;
    font-weight: bolder;
    color:#34495e

}

textarea{
    width: 100%;
    line-height: 30px;
    background:transparent;
    border: none;
    outline: none;
    font-size: 1rem;
    margin: 10px 0;
    color: grey;
    font-weight:normal;
}

.MuiButton-root{
    width: 30px;
    height: 50px;
    position: absolute;
    bottom: -28px;
    left: 400px;
    cursor: pointer;
    background-color: #fff !important  ;
    box-shadow: 5px 5px 15px -5px black;
    border-radius: 50% !important;
}

.MuiButton-root .plus_sign{
    font-size: 45px !important;
    font-size: 900;
    color: #f4b400;
    cursor: pointer;
    z-index: 222;
    background:transparent;
}

.MuiButton-root:hover .plus_sign{
    color: green;
    background: transparent;
}

.note {
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 2px 5px #ccc;
    padding: 10px;
    width: 240px;
    margin: 16px;
    float: left;
    position: relative;
}

.note h1{
    font-size: 1.1em;
    margin-bottom: 6px;
    color: #34495e;
}

.note p{
    font-size: 1.1em;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
    color: grey;

}

.note .btn{
    position: relative;
    float: right;
    margin-right: 10px;
    color: #f4b400;
    width: 50px;
    height: 50px;
    background: transparent;
    cursor: pointer;
    outline: none;
    box-shadow: 3px 3px 15px -10px rgba(0, 0,0, 0.3);
}

.deleteIcon{
    width: 0.7em;
    height: 0.7em;
    position: absolute;
    top: 1px;
    right: 400px;
}


.note .btn:hover{
    background-color: #f5ba13;
}

.note .btn:hover.deleteIcon{
    color: #fff;
}
.deleteIcon:hover{
    color: red;
}

footer{
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: auto;
}

@media screen and (max-width: 768px) {
    .header, .main_note{
      width: 100%; /* The width is 100%, when the viewport is 800px or smaller */
    }
    .header{
        padding: 0px;
    }
    .MuiButton-root{
        width: 20px;
        height: 40px;
        position: absolute;
        bottom: -28px;
        left: 250px;
        cursor: pointer;
        background-color: #fff !important  ;
        box-shadow: 5px 5px 15px -5px black;
        border-radius: 50% !important;
    }
    .note {
        /* background: #fff;
        border-radius: 7px;
        box-shadow: 0 2px 5px #ccc; */
        /* padding: 10px; */
        width: 80%;
        /* margin: 16px; */
        /* position: relative; */
    }
    
    
    
    
    .deleteIcon{
        width: 0.7em;
        height: 0.7em;
        position: absolute;
        top: 1px;
        left:-240px;
    }
    footer {
        clear: both;
        position: relative;
        height: 140%;
        margin-top: 140%;
        width: 100%;
    }
  }